<template>
	<!-- video begin-->
	<div class="video">
		<!-- video begin-->
		<div class="video-cancle">
			<van-icon name="arrow-left" size="20" @click="toRecomments"/>
		</div>
		<div class="video-box">
			<!-- 自动播放要静音muted -->
			
			<video :src="data.url" controls autoplay muted></video>
		</div>
		<!-- 简介和评论 -->
		<div class="detail-comment" color="#e44b3d">
			<van-tabs v-model:active="active" color="red">
				<van-tab title="简介">
					<div class="content" v-if="data.other.title">
						<div class="title">{{data.other.title}}</div>
						<div class="tag-type-time-view">
							<div class="tag">
								<span v-for="(item,index) in data.other.categories"
									:key="index">{{item.sub.category_name}}
									<i v-if="index!=data.other.categories.length-1">-</i>
								</span>
							</div>
							<div class="time">{{getYear(data.other.publish_time*1000)}}</div>
							<div class="view">{{handlerCount(data.other.count.count_view)}}次数</div>
						</div>
						<div class="desc">{{data.other.share_info.description}}
							<div class="good-collect-share">
								<div class="good-item">
									<van-icon name="good-job-o" size="20" />
									<span>{{handlerCount(data.other.count.count_like)}}</span>
								</div>
								<div class="good-item">
									<van-icon name="good-job-o" size="20" />
									<span>{{handlerCount(data.other.count.count_collect)}}</span>
								</div>
								<div class="good-item">
									<van-icon name="good-job-o" size="20" />
									<span>{{handlerCount(data.other.count.count_share)}}</span>
								</div>
							</div>
							<div class="all-tags">
								<span v-for="(item,index) in data.other.tags" :key="item.id">{{item.name}}</span>

							</div>
							<div class="composer">
								<div class="composer-value">创作人</div>
								<div class="composer-box">
									<div class="composer-item">
										<div class="img-info">
											<img :src="data.other.author.userinfo.avatar" />
											<div class="info">
												<div class="author" size="10">{{data.other.author.userinfo.username}}
												</div>
												<div class="author-type">{{data.other.author.userinfo.occupation}}</div>
											</div>
											<div class="btn">关注</div>
										</div>


									</div>
								</div>
							</div>


						</div>
					</div>
					<div class="sameVideo">
						<div class="title-same">相似视频</div>
						<div class="list-box">
							<div class="list-item" v-for="(item,index) in data.list" :key="item.id"
								@click="getData(item.id,item.vid)">
								<div class="item-left">
									<img :src="item.cover" />
									<span>{{handlerTime(item.duration)}}</span>
								</div>
								<div class="item-right">
									<div class="item-title">{{item.title}}</div>
									<div class="view-like">
										<span>播放量{{handlerCount(item.count.count_view)}}</span>
										<span>喜欢{{handlerCount(item.count.count_like)}}</span>
									</div>
									<div class="author-box">
										<img class="picture" :src="item.author.userinfo.avatar" height="30px" />
										<span class="username">{{item.author.userinfo.username}}</span>
									</div>
								</div>
							</div>
						</div>
					</div>
				</van-tab>
				<van-tab title="评论">
					<template #title>评论<span class="count">{{data.comments.length}}</span></template>
					<div class="comments">
						<div class="comment-item" v-for="(item,index) in data.comments" :key="item.id">
							<div class="comment-top">
								<img class="comment-pictrue" :src="item.userInfo.avatar" />
								<div class="comment-header">
									<div class="content-view">
										<div class="username">{{item.userInfo.username}}</div>
										<div class="doint">
											<van-icon name="ellipsis" size="15"/>
										</div>
									</div>

									<div class="comment-value">{{item.content}}</div>
								</div>

							</div>
							<div class="comment-bottom">
								<div class="item-address">
									<span>{{getYear(item.addtime*1000)}}.</span>
									<span>地址：{{item.ip_location}}</span>
									<div class="other-comment-good">
										<van-icon name="chat-o" />{{item.userInfo.count.count_article}}
										<van-icon name="good-job-o" />{{item.userInfo.count.count_like}}
									</div>
								</div>
							</div>
						</div>
					</div>
					<!-- 					<div class="comment-item" v-for="(item,index) in data.comments" :key="item.id">
						<div class="img-authorname-discussion">
							<div class="img-authorname-position">
								<img :src="item.userInfo.avatar"/>
								<span>{{item.userInfo.username}}</span>
							    <div class="position" >地址：{{item.ip_location}}</div>
							</div>
							<div class="discussion">{{item.content}}</div>
							
							
						</div>
					</div> -->

				</van-tab>
			</van-tabs>
		</div>

	</div>
	<!-- video end-->
</template>

<script setup>
	//引入时间样式
	import {
		getYear,
		handlerCount,
		handlerTime
	} from "@/common/handler"
	//引用样式
	import '@/assets/less/video.less'
	//引用接口
	import {
		getVideo,
		getVideoUrl,
		getVideoSame,
		getComment,
	} from "@/api/request.js";
	//引入生命周期
	import {
		onMounted,
		reactive,
		ref
	} from "vue"
	//获取路径的id
	import {
		useRoute,
		useRouter
	} from "vue-router"
	let Route = useRoute();
	console.log('Route', Route)

	//获取视频 路径 标题 上映时间 播放次数
	let data = reactive({
		url: "",
		other: { //对象属性报错但能显示

		},
		list: [], //相似视频的值
		comments: [] //评论
	})
	//获取数据的方法
	let getDetail = (id) => {
		getVideo(id).then(res => {
			console.log(res)
			data.other = res.data.data;

		}).catch(err => {
			console.log(err)
		})
	}
	//获取视频路径
	let getUrl = (vid) => {
		getVideoUrl(vid).then(res => {
			console.log('url', res)
			data.url = res.data.data.resource.progressive[0].backupUrl;
			console.log("值", data.url)
		}).catch(err => {
			console.log(err)
		})
	}
	//获取视频数据方法
	let getSame = (id) => {
		getVideoSame(id).then(res => {
			console.log('相似视频', res)
			data.list = res.data.data.list
		}).catch(err => {
			console.log('相识视频err', err)
		})
	}

	let getVideoComment = (id, page) => {
		getComment(id, page).then(res => {
			console.log('评论', res)
			data.comments = res.data.data.list
		}).catch(err => {
			console.log('评论err', err)
		})
	}
	//重新获取相似视频数据
	let getData = (id, vid) => {
		getDetail(id)
		getUrl(vid)
		getSame(id)
		window.scroll(0, 0) //滚动到顶部
	}
	//生命周期
	onMounted(() => {
		//调用数据的方法
		getDetail(Route.query.id)
		getUrl(Route.query.vid)
		getSame(Route.query.id)
		getVideoComment(Route.query.id, 1)

	})
	
	
	let Router = useRouter();
	let toRecomments = () => {
		Router.back(-1)  //返回上一页
		// Router.push({
		// 	path: '/recommend'
		// })
	}
	
	
	
</script>

<style lang="less" scoped>

</style>